@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700&display=swap');
.EditPet {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
}

.EditPet nav {
    display: flex;
    height: 80px;
    width: 100%;
    background: #1b1b1b;
    align-items: center;
    justify-content: space-between;
    padding: 0 50px 0 100px;
    flex-wrap: wrap;
}

.EditPet nav .logo {
    color: #fff;
    font-size: 35px;
    font-weight: 600;
}

.EditPet nav ul {
    display: flex;
    flex-wrap: wrap;
    list-style: none;
}

.EditPet nav ul li {
    margin: 0 5px;
}

.EditPet nav ul li a {
    color: #f2f2f2;
    text-decoration: none;
    font-size: 18px;
    font-weight: 500;
    padding: 8px 15px;
    border-radius: 5px;
    letter-spacing: 1px;
    transition: all 0.3s ease;
}

.EditPet nav ul li a.active,
.EditPet nav ul li a:hover {
    color: #111;
    background: #fff;
}

.EditPet nav .menu-btn i {
    color: #fff;
    font-size: 22px;
    cursor: pointer;
    display: none;
}

.EditPet input[type="checkbox"] {
    display: none;
}

@media (max-width: 1000px) {
    .EditPet nav {
        padding: 0 40px 0 50px;
    }
}

@media (max-width: 920px) {
    .EditPet nav .menu-btn i {
        display: block;
    }
    .EditPet #click:checked~.menu-btn i:before {
        content: "\f00d";
    }
    .EditPet nav ul {
        position: fixed;
        top: 80px;
        left: -100%;
        background: #111;
        height: 100vh;
        width: 100%;
        text-align: center;
        display: block;
        transition: all 0.3s ease;
    }
    .EditPet #click:checked~ul {
        left: 0;
    }
    .EditPet nav ul li {
        width: 100%;
        margin: 40px 0;
    }
    .EditPet nav ul li a {
        width: 100%;
        margin-left: -100%;
        display: block;
        font-size: 20px;
        transition: 0.6s cubic-bezier(0.68, -0.55, 0.265, 1.55);
    }
    .EditPet #click:checked~ul li a {
        margin-left: 0px;
    }
    .EditPet nav ul li a.active,
    .EditPet nav ul li a:hover {
        background: none;
        color: cyan;
    }
}

.EditPet .content {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    z-index: -1;
    width: 100%;
    padding: 0 30px;
    color: #1b1b1b;
}

.EditPet .content div {
    font-size: 40px;
    font-weight: 700;
}

